body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(254, 222, 65, 0.928);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  height: 100vh;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-box {
  width: 90%;
  max-width: 300px;
  height: auto;
  min-height: 100px;
  background-color: white;
  padding: 15px;
  border-radius: 25px;
}
.question {
  text-align: center;
  font-weight: 700;
}
.answers-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.progression-wrapper {
  border: 1px rgb(185, 185, 185) solid;
  background-color: rgb(228, 225, 225);
  border-radius: 15px;
  width: 90%;
  height: 15px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  box-shadow: 10px;
}
.aswer {
  padding: 10px;
  width: 50%;
  border: 1px solid black;
  border-radius: 10px;
  transition: all 0.5s;
}
.aswer:hover {
  background-color: rgb(246, 246, 246);
  transition: all 0.5s;
  cursor: pointer;
}
.progression {
  /* width: 50%; */
  background: rgb(69, 181, 181);
  background: linear-gradient(90deg, rgba(69, 181, 181, 1) 0%, rgba(0, 126, 226, 1) 100%);
  height: 15px;
  border-radius: 15px;
  transition: all 0.5s;
}

.finish_main-box {
  width: 90%;
  max-width: 300px;
  height: auto;
  min-height: 100px;
  background-color: white;
  padding: 15px;
  border-radius: 25px;
}
.image_done {
